.aside {
  position: absolute;
  width: 18%;
  top: 2.5%;
  margin-left: 1.5%;
  height: 95vh;
  text-align: center;
  border: solid 1px #282C2F;
  border-radius: 5px;
  background-color: rgba(56, 59, 61, .9);
  padding: 20px;
  color: #fff;
  -webkit-animation: asideslide 1s;
  -moz-animation: asideslide 1s;
  animation: asideslide 1s;
}

.asidehome {
  position: absolute;
  transition-delay: 5s;
  top: 20%;
  margin-left: 28%;
  height: 50vh;
  width: 40%;
  text-align: center;
  border: solid 1px #282C2F;
  border-radius: 5px;
  background-color: rgba(56, 59, 61, .9);
  padding: 20px;
  color: #fff;
}

.miniaside {
  position: absolute;
    width: 18%;
    top: 2.5%;
    margin-left: 1.5%;
    height: 12vh;
    text-align: center;
    border: solid 1px #282C2F;
    border-radius: 5px;
    background-color: rgba(56, 59, 61, .9);
    padding: 20px;
    color: #fff;
    animation: asideslide 1s;
}

@keyframes asideslide {
  0% {
    margin-left: 28%;
    width: 40%;
    top: 20%;
    height: 50vh;
  }

  100% {
    margin-left: 1.5%;
    width: 375px;
    top: 2.5%;
    height: 95vh;
  }
}

.header {
  font-family: 'Rock Salt', cursive;
  margin-bottom: 7%;
}

.navbtn {
  margin: 1%;
  margin-bottom: 2%;
}

.profileimg {
  border-radius:50%;
  width: 20%;
}

.highlights {
  margin-bottom: 0;
}

.buttons {
  margin: 1%;
}

.nameinput{
  display:inline-block;
  margin:1%;
}

.avatarinput {
  display: inline-block;
  margin: 1%;
}

input[type=checkbox]{
    display:none;
}

input[type=checkbox] + label {
		display:inline-block;
		margin:2px;
		padding: 4px 12px;
		margin-bottom: 0;
		font-size: 14px;
		line-height: 20px;
		color: #333;
		cursor: pointer;
		background-color: #6E777D;
    border: 2px solid #6E777D;
    color: #fff;
    border-radius: 5px;
	}

input[type=checkbox]:checked + label{
     background-color:#242729;
 }

 @media only screen and (max-width: 500px){
   [id='aside'] {
     width: 100%;
     margin:0;
     height: 45vh;
     top: 55%;

   }
 }

.togglebtn{
  margin-left: 90%;
}

.directionsbtn {
  background: none!important;
  border: none;
  margin-bottom: 1%;
  padding: 0!important;
  font-family: arial, sans-serif;
  color: #fff;
  text-decoration: underline;
  cursor: pointer;
}
